import { Text, Block } from '@ali/4ever-cangjie';

const isEmptyParagraph = (block: Block): boolean => {
  return block.type === 'paragraph'
  && (block.data.jc || 'left') === 'left'
  && (!block.data.ind || block.data.ind.left === 0)
  && !block.data.list
  && Text.isTextList(block.nodes)
  && block.text === '';
};

export default isEmptyParagraph;
